<template>
    <div>
        <div class="box">
            <h2>消息提示</h2>
            <el-button type="text" @click="open">点击打开 Message Box</el-button>
        </div>
        <div class="box">
            <h2>确认消息</h2>
            <el-button type="text" @click="open2">点击打开 Message Box</el-button>
        </div>
        <div class="box">
            <h2>提交内容</h2>
            <el-button type="text" @click="open3">点击打开 Message Box</el-button>
        </div>
    </div>
</template>
<script>
export default {
    methods: {
        open () {
            this.$alert('这是一段内容', '标题名称', {
                confirmButtonText: '确定',
                callback: action => {
                    this.$message({
                        type: 'info',
                        message: `action: ${action}`
                    })
                }
            })
        },
        open2 () {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$message({
                    type: 'success',
                    message: '删除成功!'
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                })
            })
        },
        open3 () {
            this.$prompt('请输入邮箱', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
                inputErrorMessage: '邮箱格式不正确'
            }).then(({value}) => {
                this.$message({
                    type: 'success',
                    message: '你的邮箱是: ' + value
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '取消输入'
                })
            })
        }
    }
}
</script>
<style scoped>
.el-row {
    margin-bottom: 20px;
    &:last-child {
        margin-bottom: 0;
    }
}
.el-col {
    border-radius: 4px;
}

.box {
    margin-bottom: 20px;
}
</style>
/*不用看*/
<style>
/*按钮总样式覆盖*/
.el-button {
    font-size: 12px;
    padding: 9px 16px;
    border-radius: 2px;
}
/*按钮size样式*/
.el-button--medium {
    padding: 0px 16px;
    height: 40px;
    line-height: 38px;
    font-size: 14px;
    min-width: 104px;
}
.el-button--medium.is-round {
    padding: 0px 20px;
}
.el-button--mini {
    padding: 0px 8px;
    height: 24px;
    line-height: 22px;
    font-size: 12px;
    min-width: 56px;
}
.el-button--mini.is-round {
    padding: 0px 15px;
}
/* 朴素按钮的颜色覆盖*/
.el-button.is-plain:focus,
.el-button.is-plain:hover {
    outline: none;
    text-decoration: none;
    color: #1acac6;
    background: #FFF;
    border-color: #1acac6;
}
/*禁止类按钮样式*/
.el-button.is-disabled,
.el-button.is-disabled:focus,
.el-button.is-disabled:hover {
    cursor: not-allowed;
    background: #f8f8f8;
    color: #ccc;
    border: 1px solid #ebeef5;
    -ms-transform: none;
    transform: none;
}
/*primary 类按钮样式修改*/
.el-button--primary {
    color: #fff;
    background-color: #00C4C0;
    border-color: #00C4C0;
}
.el-button--primary:focus, .el-button--primary:hover {
    background: #1acac6;
    border-color: #1acac6;
    color: #fff;
}
/*text样式覆盖*/
.el-button--text.is-disabled,
.el-button--text.is-disabled:focus,
.el-button--text.is-disabled:hover {
    border-color: transparent;
    color: #c0c4cc;
    cursor: not-allowed;
    background-image: none;
    background-color: #fff;
}
.el-button--text {
    color: #00C4C0;
}
.el-button--text:active,
.el-button--text:focus,
.el-button--text:hover {
    color: #1acac6;
    border-color: transparent;
    background-color: transparent;
}
/*危险累按钮样式覆盖*/
.el-button--danger.is-plain {
    color: #F86B6B;
    background: #fff;
    border-color: #F86B6B;
}
.el-button--danger.is-plain:focus, .el-button--danger.is-plain:hover {
    background: #fff;
    border-color: #f97a7a;
    color: #f97a7a;
}
/*loading按钮样式*/
.el-button.is-loading {
    cursor: not-allowed;
    background: #f8f8f8;
    color: #ccc;
    border: 1px solid #eee;
    -ms-transform: none;
    transform: none;
}
.el-input.is-active .el-input__inner,
.el-input__inner:focus {
    border-color: #00C4C0;
    outline: 0;
}
</style>

<style>
/*样式覆盖*/
.el-message-box {
    border-radius: 0px;
    padding-bottom: 0px;
}
.el-message-box__header {
    padding: 15px 16px;
    border-bottom: 1px solid #eee;
}
.el-message-box__title {
    font-size: 16px;
    line-height: 24px;
}
.el-message-box__content {
    padding: 24px;
    min-height: 12px;
    overflow: auto;
    font-size: 12px;
    color: rgba(0, 0, 0, 0.65);
    line-height: 24px;
}
.el-message-box__btns {
    padding: 10px 15px;
    text-align: right;
    border-top: 1px solid #e3e3e3;
    font-size: 0;
}
</style>



